<template>
  <div id="edm-component">
    <h2 class="zoom-title">Choose Template</h2>
    <el-row :gutter="30">
      <el-col :span="8">
        <EDMTemplateCard>
          <Template1></Template1>
        </EDMTemplateCard>
        <p class="name">Template 1</p>
      </el-col>
      <el-col :span="8">
        <EDMTemplateCard></EDMTemplateCard>
        <p class="name">Template 1</p>
      </el-col>
      <el-col :span="8">
        <EDMTemplateCard></EDMTemplateCard>
        <p class="name">Template 1</p>
      </el-col>
    </el-row>
    <h2 class="zoom-title">Saved EDM</h2>
    <el-row :gutter="30">
      <el-col v-for="item in list" :key="item.id" :span="8">
        <EDMTemplateCard editable :data="item"></EDMTemplateCard>
        <p class="name">{{ item.subject }}</p>
      </el-col>
    </el-row>
  </div>
</template>

<script lang="ts">
import { defineComponent, computed, onMounted } from "vue";
import { useStore } from "vuex";

import EDMTemplateCard from "@/components/EDMTemplateCard.vue";
import Template1 from "./templates/Template1.vue";
import { GetEdms } from "@/typings/store";

const Component = defineComponent({
  name: "EDM",
  components: {
    EDMTemplateCard,
    Template1
  },
  setup() {
    const store = useStore();

    const list = computed(() => store.state.edm.list);

    const init = async () => {
      await store.dispatch(GetEdms);
    };

    onMounted(async () => await init());

    return {
      list
    };
  }
});

export default Component;
</script>

